*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Righteous", cursive;
  content: "";
  clear: both;
  margin: 0;
}
body {
  text-align: center;
  justify-items: center;
  align-items: center;
}
h1,
p,
label,
input,
button {
  margin-top: 1em;
}
input {
  display: block;
  width: 50%;
  margin: 0 auto;
}
.searchBoxWrapper {
  max-width: 800px;
  margin: 1em auto;
  border: 5px solid rgb(189, 206, 186);
  padding: 4em 0;
  text-align: center;
}
.weather {
  max-width: 800px;
  margin: 10em auto;
  border: 5px solid rgb(147, 188, 139);
}
